<template>
    <div>
        <div>
            <el-input placeholder="请输入角色英文名" v-model="role.name" size="small" style="width: 260px;margin-right: 10px">
                <template slot="prepend">ROLE_</template>
            </el-input>
            <el-input placeholder="请输入角色中文名" v-model="role.namezh" size="small"
                      style="width: 230px;margin-right: 10px"></el-input>
            <el-button size="small" type="primary" icon="el-icon-circle-plus-outline" @click="addRole">添加角色</el-button>
        </div>
        <div style="width: 70%;margin-top: 20px">
            <el-collapse accordion v-model="activeName" @change="change">
                <el-collapse-item :title="r.namezh" :name="r.id" v-for="(r,index) in roles" :key="index">
                    <el-card class="box-card">
                        <div slot="header">
                            <span>可访问的资源</span>
                            <el-button style="float: right; color: red; padding: 3px 0" icon="el-icon-delete"
                                       type="text" size="medium" @click="delRole(r)"></el-button>
                        </div>
                        <div>
                            <!--:props="defaultProps"它的意思就是说树状图默认子类名叫什么还有默认的文本用哪个属性
                             ref="tree"相当于起名字
                             根据当前的角色去查找默认它有哪些资源:default-checked-keys-->
                            <el-tree show-checkbox
                                     node-key="id"
                                     ref="tree"
                                     :default-checked-keys="selectMenus"
                                     :data="allMenus" :props="defaultProps"
                                     :key="index"></el-tree>
                            <div style="display: flex;justify-content: flex-end">
                                <el-button size="small" type="success" @click="cancelUpdate">取消修改</el-button>
                                <el-button size="small" type="primary" @click="updateMenus(r.id,index)">确认修改</el-button>
                            </div>
                        </div>
                    </el-card>
                </el-collapse-item>
            </el-collapse>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PerMissMana",
        data() {
            return {
                role: {
                    name: '',
                    namezh: ''
                },
                roles: [],
                allMenus: [],
                selectMenus: [],
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                activeName: -1
            }
        }, mounted() {
            this.initRoles();
        }
        , methods: {
            cancelUpdate() {
                this.activeName = -1;
            },
            delRole(role){
                this.$confirm('此操作将永久删除【'+role.namezh+'】角色, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequset('/system/basic/permiss/'+role.id).then(resp=>{
                        if(resp){
                            this.initRoles();
                        }

                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            updateMenus(rid, index) {
                let tree = this.$refs.tree[index];
                let selectKeys = tree.getCheckedKeys(true);
                let url = "/system/basic/permiss/?rid=" + rid;
                selectKeys.forEach(key => {
                    url += "&mids=" + key;
                })
                this.putRequset(url).then(resp => {
                    if (resp) {
                        this.initRoles();
                        this.activeName = -1;
                    }
                })
            },
            change(rid) {
                if (rid) {
                    this.initMenus();
                    this.initSelectMenus(rid);
                }
            },
            initSelectMenus(rid) {
                this.getRequset("/system/basic/permiss/mids/" + rid).then(resp => {
                    this.selectMenus = resp;
                })
            }
            ,
            initMenus() {
                this.getRequset("/system/basic/permiss/meuns").then(resp => {
                    this.allMenus = resp;
                })
            },
            initRoles() {
                this.getRequset("/system/basic/permiss/").then(resp => {
                    this.roles = resp;
                })
            },
            addRole() {
                if (this.role.name && this.role.namezh) {
                    this.postReqeust("/system/basic/permiss/addRole", this.role).then(resp => {
                        if (resp) {
                            this.initRoles();
                            this.role.namezh = '';
                            this.role.name = '';
                        }
                    })
                } else {
                    this.$message.error("数据不可以为空");
                }

            }

        }
    }
</script>

