<template>
    <div>
        <div style="margin-top: 10px;display: flex;justify-content: center">
            <el-input size="small" v-model="keywords" placeholder="通过用户名搜索用户..."
                      prefix-icon="el-icon-notebook-1" style="width: 400px;margin-right: 10px" @keydown.enter.native="doSearch"></el-input>
            <el-button icon="el-icon-search" size="small" type="primary" @click="doSearch">搜索</el-button>
        </div>
        <div class="hr-container">
            <el-card class="box-card" v-for="(hr,index) in hrs" :key="index">
                <div slot="header">
                    <span>{{hr.name}}</span>
                    <el-button style="float: right; padding: 3px 0" type="text"
                               icon="el-icon-delete" size="medium" @click="deleteHr(hr.id)"></el-button>
                </div>
                <div>
                    <div style="display: flex;justify-content: center">
                        <img :src="hr.userface" :alt="hr.name" :title="hr.name" class="userfaceimg">
                    </div>
                    <div class="hrinfo">用户名：{{hr.name}}</div>
                    <div class="hrinfo">用户号码：{{hr.phone}}</div>
                    <div class="hrinfo">电话号码：{{hr.telephone}}</div>
                    <div class="hrinfo">地址：{{hr.address}}</div>
                    <div class="hrinfo">用户状态：
                        <el-switch
                                @change="changeEnable(hr)"
                                v-model="hr.enabled"
                                active-text="禁用"
                                inactive-text="启用"
                                active-color="#13ce66"
                                inactive-color="#ff4949">
                        </el-switch>
                    </div>
                    <div class="hrinfo">用户角色：
                        <el-tag v-for="(role,indexj) in hr.roles" size="small" type="success" :key="indexj"
                                style="margin-right: 4px;">
                            {{role.namezh}}
                        </el-tag>

                        <el-popover
                                @show="showPop(hr)"
                                @hide="hidePop(hr)"
                                placement="right"
                                title="角色列表"
                                width="200"
                                trigger="click">
                            <el-select v-model="selectedRoles" multiple placeholder="请选择">
                                <el-option
                                        v-for="(r,indexk) in allRoles"
                                        :key="indexk"
                                        :label="r.namezh"
                                        :value="r.id">
                                </el-option>
                            </el-select>

                            <el-button type="text" icon="el-icon-more-outline" slot="reference"></el-button>
                        </el-popover>

                    </div>
                    <div class="hrinfo">
                        备注：{{hr.remark}}
                    </div>
                </div>
            </el-card>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SysHr",
        data() {
            return {
                keywords: '',
                hrs: [],
                allRoles: [],
                selectedRoles: []
            }
        }, mounted() {
            this.initHrs();
        }
        ,
        methods: {
            deleteHr(id) {
                this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequset("/system/hr/" + id).then(resp => {
                        if (resp) {
                            this.initHrs();
                        }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            changeEnable(hr) {
                delete hr.roles;
                this.putRequset("/system/hr/", hr).then(resp => {
                    if (resp) {
                        this.initHrs();
                    }
                })
            },
            initHrs() {
                this.getRequset("/system/hr/?keywords="+this.keywords).then(resp => {
                    this.hrs = resp;
                })
            },
            initAllRoles() {
                this.getRequset("/system/hr/roles").then(resp => {
                    this.allRoles = resp;
                })
            },
            showPop(hr) {
                this.initAllRoles();
                let roles = hr.roles;
                this.selectedRoles = [];
                roles.forEach(r => {
                    this.selectedRoles.push(r.id);
                })

            },
            hidePop(hr) {
                let roles = []
                Object.assign(roles,hr.roles);
                let flag = false
                if (roles.length != this.selectedRoles.length) {
                    flag = true;
                } else {
                    for (let i = 0; i < roles.length; i++) {
                        let role = roles[i];
                        for (let j = 0; j < this.selectedRoles.length; j++) {
                            let sr = this.selectedRoles[j];
                            if (role.id == sr) {
                                roles.splice(i, 1);    // 移除
                                i--;
                                break;
                            }
                        }
                    }
                    if(roles.length!=0){
                        flag=true;
                    }
                }
                if (flag) {
                    let url = '/system/hr/role?hrid=' + hr.id;
                    this.selectedRoles.forEach(sr => {
                        url += '&rids=' + sr;
                    })
                    this.putRequset(url).then(resp => {
                        if (resp) {
                            this.initHrs();
                        }
                    })
                }
            },
            doSearch(){
                this.initHrs();
            }
        }
    }
</script>

<style>

    .hr-container {
        margin-top: 20px;
        display: flex;
        flex-wrap: wrap; /*//自动换行属性*/
        justify-content: space-around;
        /*用于设置或检索弹性盒子元素在主轴（横轴）方向上的对齐方式。*/
    }

    .box-card {
        width: 350px;
        margin-bottom: 20px;
    }

    .userfaceimg {
        height: 72px;
        width: 72px;
        border-radius: 72px;
    }

    .hrinfo {
        color: #17c2ff;
        font-size: 12px;
    }
</style>