<template>
    <div>
        <div>
            <el-input
                    placeholder="添加职称..."
                    prefix-icon="el-icon-search"
                    v-model="job.name"
                    @keydown.enter.native="addJob"
                    size="small" style="width: 240px;margin-right: 8px">
            </el-input>
            <el-select v-model="job.titleLevel" clearable placeholder="请选择职称等级" size="small" style="margin-right: 8px">
                <el-option value="正高级"></el-option>
                <el-option value="副高级"></el-option>
                <el-option value="高级"></el-option>
                <el-option value="中级"></el-option>
                <el-option value="初级"></el-option>
            </el-select>
            <el-button type="success" icon="el-icon-circle-plus-outline" size="small" plain @click="addJob">添加
            </el-button>
        </div>
        <div style="width: 70%;margin-top: 20px">
            <el-table
                    :data="jobData"
                    stripe
                    size="small"
                    border
                    style="width: 100%">
                <el-table-column
                        prop="id"
                        label="编号"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="职称名称"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="titleLevel"
                        label="职称级别"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="createDate"
                        label="创建时间"
                        width="150">
                </el-table-column>
                <el-table-column
                        label="是否启用"
                        width="150">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.enabled">已启用</el-tag>
                        <el-tag type="danger" v-else>未启用</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label="操作"
                        width="200">
                    <template slot-scope="scope">
                        <el-button type="primary" size="small" @click="toEditJob(scope.row)">编辑</el-button>
                        <el-button type="danger" @click="delJob(scope.row)" size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-dialog
                    title="职称修改"
                    :visible.sync="dialogVisible"
                    width="40%"
            >
                <div style="width: 800px">
                    <el-tag style="margin-right: 8px">职称名称</el-tag>
                    <el-input
                            v-model="updatejob.name"
                            size="small" style="width: 200px;margin-right: 8px">
                    </el-input>
                    <el-tag style="margin-right: 8px">职称等级</el-tag>
                    <el-select v-model="updatejob.titleLevel" clearable placeholder="请选择职称等级" size="small"
                               style="width:150px">
                        <el-option value="正高级"></el-option>
                        <el-option value="副高级"></el-option>
                        <el-option value="高级"></el-option>
                        <el-option value="中级"></el-option>
                        <el-option value="初级"></el-option>
                    </el-select>

                    <div style="margin-top: 30px">
                        <el-tag style="margin-right: 20px">是否启用</el-tag>
                        <el-switch
                                v-model="updatejob.enabled"
                                active-text="启用"
                                inactive-text="禁用">
                        </el-switch>
                    </div>
                </div>
                <span slot="footer" class="dialog-footer">
                    <el-button size="small" @click="dialogVisible = false">取 消</el-button>
                    <el-button size="small" type="primary" @click="editJob">确 定</el-button>
                </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
    export default {
        name: "JobLevelMana",
        data() {
            return {
                job: {
                    name: '',
                    titleLevel: ''
                },
                updatejob: {
                    name: '',
                    titleLevel: '',
                    enabled:false
                },
                jobData: [],
                dialogVisible: false
            }
        },
        mounted() {
            this.initJob();
        }
        , methods: {
            editJob() {
                this.putRequset("/system/basic/job/", this.updatejob).then(resp => {
                    if (resp) {
                        this.updatejob.name = '';
                        this.updatejob.titleLevel = '';
                        this.dialogVisible = false;
                        this.initJob();
                    }
                })
            },
            toEditJob(data) {
                Object.assign(this.updatejob, data);
                this.dialogVisible = true;
            },
            delJob(data) {
                this.$confirm('此操作将永久删除该条记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequset("/system/basic/job/" + data.id).then(resp => {
                        if (resp) {
                            this.initJob();
                        }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            addJob() {
                if (this.job.name == null || this.job.name == '') {
                    this.$message.error("职称名字不能为空！");
                    return
                }
                if (this.job.titleLevel == null || this.job.titleLevel == '') {
                    this.$message.error("请选择职称等级！");
                    return
                }
                this.postReqeust("/system/basic/job/", this.job).then(resp => {
                    if (resp) {
                        this.initJob();
                        this.job.name = '';
                        this.job.titleLevel = '';
                    }
                })
            },
            initJob() {
                this.getRequset("/system/basic/job/").then(resp => {
                    this.jobData = resp;
                })
            }
        }
    }
</script>

