<template>
    <div style="width: 500px">
        <el-input
                prefix-icon="el-icon-search"
                placeholder="请输入部门名称进行搜索"
                v-model="filterText">
        </el-input>

        <el-tree
                class="filter-tree"
                :data="deps"
                :props="defaultProps"
                :filter-node-method="filterNode"
                ref="tree"
                :expand-on-click-node="false">
            <!--node指当前这个元素,data值这个元素的数据-->
            <span class="custom-tree-node" slot-scope="{ node, data }"
                  style="display: flex;justify-content: space-between;width:100%">
        <span>{{ data.name }}</span>
        <span>
          <el-button
                  type="primary"
                  size="mini"
                  class="depbut"
                  @click="() => showAddDepView(data)">
     添加部门
          </el-button>
          <el-button
                  class="depbut"
                  type="danger"
                  size="mini"
                  @click="() => deleteDep( data)">
          删除部门
          </el-button>
        </span>
      </span>
        </el-tree>
        <el-dialog
                title="添加部门"
                :visible.sync="dialogVisible"
                width="30%"
                :before-close="handleClose">

            <div>
                <table>
                    <tr>
                        <td>
                            <el-tag>上级部门</el-tag>
                        </td>
                        <td><span>{{pname}}</span></td>
                    </tr>
                    <tr>
                        <td>
                            <el-tag>部门名称</el-tag>
                        </td>
                        <td>
                            <el-input v-model="dep.name" placeholder="请输入部门名称"></el-input>
                        </td>
                    </tr>
                </table>
            </div>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="doAppDep">确 定</el-button>
  </span>

        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "DepMana"
        , data() {
            return {
                filterText: '',
                deps: [],
                pname: '',
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                dialogVisible: false,
                dep: {
                    name: '',
                    parentId: -1
                }
            }
        },
        //当filterText这个变量值发生变化就会触发这个方法val是最新值
        // this.$refs.tree.filter(val);调用的就是树形绑定的   :filter-node-method="filterNode"这个方法
        watch: {
            filterText(val) {
                this.$refs.tree.filter(val);
            }
        },
        methods: {
            //这个方法return true就是代表当前这个data数据还留着,否则就是反的
            filterNode(value, data) {
                if (!value) return true;
                return data.name.indexOf(value) !== -1;
            }
            , initDep() {
                this.getRequset("/system/basic/department/").then(resp => {
                    if (resp) {
                        this.deps = resp;
                    }
                })
            },
            showAddDepView(data) {
                this.dialogVisible = true;
                this.pname = data.name;
                this.dep.parentId = data.id;
            }, deleteDep(data) {
                if(data.parent){
                    this.$message.error("父部门删除失败");
                }else{
                    this.$confirm('此操作将永久删除该部门, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.deleteRequset("/system/basic/department/"+data.id).then(resp=>{
                            if(resp){
                                this.removeDeps(null,this.deps,data.id);
                            }
                        })
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                    });
                }
            },
            addDep2Deps(deps, dep) {
                for (let i = 0; i < deps.length; i++) {
                    let d = deps[i];
                    if(d.id==dep.parentId){
                        d.children=d.children.concat(dep);
                        return;
                    }else{
                        this.addDep2Deps(d.children,dep);
                    }
                }
            },
            doAppDep() {
                this.postReqeust("/system/basic/department/", this.dep).then(resp => {
                    if (resp) {
                        this.addDep2Deps(this.deps, resp.obj);
                        this.initdeps();
                    }
                })
            },
            initdeps() {
                this.dep.name = ''
                this.dep.parentId = -1
                this.dialogVisible = false;
                this.pname='';

            } ,removeDeps(p,deps,id){
                for(let i =0;i<deps.length;i++) {
                    let d = deps[i];
                    if(d.id==id){
                        deps.splice(i,1);
                        if(deps.length==0){
                            p.parent=false;
                        }
                    }else{
                        this.removeDeps(d,d.children,id);
                    }
                }
            },
        },
        mounted() {
            this.initDep();
        }
    }
</script>

<style scoped>
    .depbut {
        padding: 2px;
    }
</style>