<template>
    <div>
        <div>
            <el-input
                    placeholder="添加职位..."
                    prefix-icon="el-icon-circle-plus-outline"
                    v-model="pos.name"
                    @keydown.enter.native="addPosition"
                    style="width: 300px;margin-right: 20px">
            </el-input>
            <el-button type="success" icon="el-icon-s-help" plain @click="addPosition">添加</el-button>
        </div>
        <div>
            <!--是否为斑马纹 stripe
              @selection-change:点击复选框触发-->
            <el-table
                    :data="TablePos"
                    stripe
                    border
                    size="small"
                    style="width: 60%;margin-top: 10px"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="编号"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="职位名称"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="createDate"
                        label="创建时间"
                        width="230"
                >

                </el-table-column>
                <el-table-column
                        label="是否启用"
                        width="150">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.enabled">已启用</el-tag>
                        <el-tag type="danger" v-else>未启用</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label="操作"
                        width="230">
                    <template slot-scope="scope">
                        <!--slot-scope="scope" 来取得作用域插槽:data绑定的数据，scope可以随便替换其他名称，只是定义对象来代表取得的data数据，便于使用-->
                        <el-button type="primary" size="small" @click="toEditPosition(scope.row)" icon="el-icon-toilet-paper">编辑</el-button>
                        <el-button type="danger" size="small" @click="delPosition(scope.row)" icon="el-icon-no-smoking">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-button size="small" type="danger" style="margin-top: 12px" :disabled="multipleSelection.length==0"
                       @click="deletePositionsByIds">批量删除
            </el-button>
        </div>
        <!--dialogVisible是否显示-->
        <el-dialog
                title="修改职位"
                :visible.sync="dialogVisible"
                width="30%"
                 >
            <div>
                <div>  <el-tag effect="plains ">职位名称</el-tag>
                    <el-input size="small" class="updatePosInput" v-model="updatePos.name"></el-input></div>
              <div>
                  <el-tag style="margin-right: 20px">是否启用</el-tag>
                  <el-switch
                          v-model="updatePos.enabled"
                          active-text="启用"
                          inactive-text="禁用">
                  </el-switch>
              </div>
            </div>
            <span slot="footer" class="dialog-footer">
    <el-button size="small" @click="dialogVisible = false">取 消</el-button>
    <el-button size="small" type="primary" @click="editPosition">确定</el-button>
  </span>
        </el-dialog>

    </div>
</template>

<script>
    export default {
        name: "PosMana",
        data() {
            return {
                pos: {
                    name: ''
                },
                updatePos:{
                  name:''
                    ,enabled:''
                },
                TablePos: [],
                dialogVisible:false
                ,multipleSelection: []
            }
        }, mounted() {
            //#在模板渲染成html后调用
            this.init();
        }
        , methods: {
            deletePositionsByIds() {
                this.$confirm('此操作将永久删除' + this.multipleSelection.length + '条记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids = "?";
                    this.multipleSelection.forEach(item => {
                        ids += "ids=" + item.id + "&";
                    });
                    this.deleteRequset("/system/basic/pos/delPos" + ids).then(resp => {

                            this.init();

                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            editPosition(){
                this.putRequset("/system/basic/pos/",this.updatePos).then(resp=>{
                    if(resp){
                        this.dialogVisible=false;
                        this.updatePos.name='';
                        this.init();
                    }
                })
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            toEditPosition(data){
                // alert(JSON.stringify(data));
                Object.assign(this.updatePos,data);
               // this.updatePos=data;
                this.dialogVisible=true;
            },
            addPosition() {
                if (this.pos.name) {
                    this.postReqeust("/system/basic/pos/", this.pos).then(resp => {
                        if (resp) {
                            this.pos.name = '';
                            this.init();
                        }
                    })
                } else {
                    this.$message.error('职位名称不可以为空');
                }
            },delPosition(data){
                this.$confirm('此操作将永久删除'+data.name+', 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequset("/system/basic/pos/"+data.id+"").then(resp=>{
                        if(resp){
                            this.init()
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            }
            , init() {
                this.getRequset("/system/basic/pos/").then(resp => {
                    this.TablePos = resp;
                })
            }
        }
    }
</script>

<style>
    .updatePosInput{
        width: 200px;
        margin-left: 20px;
    }
</style>